import React, { useState, useEffect } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { CategoryScale, LinearScale } from 'chart.js';
import Chart from 'chart.js/auto';
import Select from 'react-select';
import Api from '../helper/Api';
import ShowChart from '../helper/ShowChart';
import '../css/DatePicker.css';
import '../css/Chart.css';
import Tooltip from '@mui/material/Tooltip';
import IconButton from '@mui/material/IconButton';
import GridOnIcon from '@mui/icons-material/GridOn';
import ViewListIcon from '@mui/icons-material/ViewList';

// Registering chart scales
Chart.register(CategoryScale, LinearScale);

function DataPage() {
  // State variables for data, KTData, availableDates, selectedDate, selectedId, and dropdownOptions
  const [data, setData] = useState([]);
  const [KTData, setKTData] = useState([]);
  const [availableDates, setAvailableDates] = useState([]);
  const [selectedDate, setSelectedDate] = useState(null);
  const [selectedId, setSelectedId] = useState(0); // Initial selected value for dropdown
  const [dropdownOptions, setDropdownOptions] = useState([]); // State variable for dropdown options
  const [gridView, setGridView] = useState(true);

  // Fetching available ID options from API and setting initial selectedId
  useEffect(() => {
    Api.getListRumahKaca() // Fetch the options from the API
      .then((res) => {
        const options = res.data.map((d) => ({ value: d.ID_Rumah_Kaca, label: d.Nama_RK }));
        setDropdownOptions(options);
        setSelectedId(options[0].value); // Set the initial selected value
      })
      .catch((err) => console.log(err));
  }, []);

  // Fetching available dates when selectedId changes
  useEffect(() => {
    // console.log('Fetching available dates...');
    Api.getAvailableDates(selectedId) // Pass selected value to the API call
      .then((res) => {
        // console.log('Available dates response:', res.data);
        const datesWithAvailableData = res.data.map((d) => d.Tanggal);
        setAvailableDates(datesWithAvailableData);
        if (datesWithAvailableData.length > 0) {
          setSelectedDate(new Date(datesWithAvailableData[0]));
        }
      })
      .catch((err) => console.log(err));
  }, [selectedId]); // Update when selectedId changes

  // Fetching data based on selectedDate and selectedId
  useEffect(() => {
    if (selectedDate) {
      Api.getDataByDate(selectedDate.toISOString().slice(0, 10), selectedId) // Pass selected value to the API call
        .then((res) => {
          setData(res.data);
        })
        .catch((err) => console.log(err));

        Api.getKTDataByDate(selectedDate.toISOString().slice(0, 10), selectedId) // Pass selected value to the API call
        .then((res) => {
          setKTData(res.data);
        })
        .catch((err) => console.log(err));
    }
  }, [selectedDate, selectedId]); // Update when selectedDate or selectedId changes

  // Function to filter dates based on availability
  const filterDate = (date) => {
    if (!date || availableDates.length === 0) {
      return false;
    }
  
    const formattedDate = date.toISOString().slice(0, 10);
    const nextDate = new Date(date);
    nextDate.setDate(nextDate.getDate() + 1);
    const formattedNextDate = nextDate.toISOString().slice(0, 10);
  
    return (
      (availableDates.includes(formattedDate) && availableDates.includes(formattedNextDate)) ||
      (!availableDates.includes(formattedDate) && availableDates.includes(formattedNextDate))
    );
  };

  // Function to handle dropdown selection change
  const handleDropdownChange = (selectedOption) => {
    setSelectedId(selectedOption.value);
  };

  // Rendering the component
  return (
    <div className="container">
      <div className="chart-container">
        <div className="input-container">
          <div className="left-column">
          <label htmlFor="dropdown">Greenhouse:</label>
              <Select
                id="dropdown"
                value={dropdownOptions.find((option) => option.value === selectedId)}
                options={dropdownOptions}
                onChange={handleDropdownChange}
              />
          </div>
          <div className="right-column">
            <label htmlFor="datepicker">Date:</label>
              <DatePicker
                id="datepicker"
                selected={selectedDate}
                onChange={(date) => setSelectedDate(date)}
                filterDate={filterDate}
                dateFormat="yyyy-MM-dd"
              />
          </div>
        </div>
        <div className="layout-toggle">
          <Tooltip title={"Grid View"}>
            <IconButton onClick={() => setGridView(true)}>
              <GridOnIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title={"Stacked View"}>
            <IconButton onClick={() => setGridView(false)}>
              <ViewListIcon />
            </IconButton>
          </Tooltip>
        </div>
        {gridView ? (
        <div className="charts-grid">
          <div className="chart">
            <ShowChart data={data} attribute={'Suhu'} mode={'Avg'} />
          </div>
          <div className="chart">
            <ShowChart data={data} attribute={'RH'} mode={'Avg'} />
          </div>
          <div className="chart">
            <ShowChart data={KTData} attribute={'KT'} mode={'Avg'} />
          </div>
          {/* Add more charts here if needed */}
        </div>
      ) : (
        <div className="charts-stack">
            <ShowChart data={data} attribute={'Suhu'} mode={'Avg'} />
            <ShowChart data={data} attribute={'RH'} mode={'Avg'} />
            <ShowChart data={KTData} attribute={'KT'} mode={'Avg'} />
          {/* Add more charts here if needed */}
        </div>
      )}
      </div>
    </div>
  );
}

export default DataPage;
