import React, { useState, useEffect } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { CategoryScale, LinearScale } from 'chart.js';
import Chart from 'chart.js/auto';
import Select from 'react-select';
import { Tooltip, IconButton } from '@mui/material';
import { HelpOutline } from "@mui/icons-material";
import Api from '../helper/Api';
import ShowChart from '../helper/ShowChart';
import '../css/PageTitle.css';
import '../css/DatePicker.css';
import '../css/Chart.css';

Chart.register(CategoryScale, LinearScale);

function MonthlyDataPage() {
  // State variables for data, KTData, availableDates, selectedDate, selectedId, and dropdownOptions
  const [data, setData] = useState([]);
  const [KTData, setKTData] = useState([]);
  const [availableDates, setAvailableDates] = useState([]);
  const [selectedDate, setSelectedDate] = useState(null);
  const [selectedId, setSelectedId] = useState(0); // Initial selected value for dropdown
  const [dropdownOptions, setDropdownOptions] = useState([]); // State variable for dropdown options

  // Fetching available ID options from API and setting initial selectedId
  useEffect(() => {
    Api.getListRumahKaca() // Fetch the options from the API
      .then((res) => {
        const options = res.data.map((d) => ({ value: d.ID_Rumah_Kaca, label: d.Nama_RK }));
        setDropdownOptions(options);
        setSelectedId(options[0].value); // Set the initial selected value
      })
      .catch((err) => console.log(err));
  }, []);

  // Fetching available dates when selectedId changes
  useEffect(() => {
    Api.getAvailableDates(selectedId) // Pass selected value to the API call
      .then((res) => {
        const datesWithAvailableData = res.data.map((d) => d.Tanggal);
        setAvailableDates(datesWithAvailableData);
        if (datesWithAvailableData.length > 0) {
          setSelectedDate(new Date(datesWithAvailableData[0]));
        }
      })
      .catch((err) => console.log(err));
  }, [selectedId]); // Update when selectedId changes

  // Fetching data for the selected month when selectedDate or selectedId changes
  useEffect(() => {
    if (selectedDate) {
      Api.getDataByMonth(selectedDate.toISOString().slice(0, 7), selectedId)
        .then((res) => {
          setData(res.data);
        })
        .catch((err) => console.log(err));
      Api.getKTDataByMonth(selectedDate.toISOString().slice(0, 7), selectedId)
        .then((res) => {
          setKTData(res.data);
        })
        .catch((err) => console.log(err));
    }
  }, [selectedDate, selectedId]); // Update when selectedDate or selectedId changes
    
  // Function to get available months from available dates
  const availableMonths = availableDates.map(date => {
    const year = new Date(date).getFullYear();
    const month = new Date(date).getMonth();
    return new Date(year, month, 1);
  });

  // Function to handle dropdown selection change
  const handleDropdownChange = (selectedOption) => {
    setSelectedId(selectedOption.value);
  };

  // Tooltip text for chart information
  const tooltipText = 'These charts display the average data for each hour during the selected month.';

  // Rendering the component
  return (
    <div className="container">
      <div className="chart-container">
        <div className="title-container">
          <h1>Monthly Data</h1>
          <Tooltip title={tooltipText}>
            <IconButton>
              <HelpOutline />
            </IconButton>
          </Tooltip>
        </div>
        <div className="input-container">
          <div className="left-column">
          <label htmlFor="dropdown">Greenhouse:</label>
              <Select
                id="dropdown"
                value={dropdownOptions.find((option) => option.value === selectedId)}
                options={dropdownOptions}
                onChange={handleDropdownChange}
              />
          </div>
          <div className="right-column">
            <label htmlFor="datepicker">Date:</label>
              <DatePicker
                id="datepicker"
                selected={selectedDate}
                onChange={(date) => setSelectedDate(date)}
                showMonthYearPicker
                dateFormat="yyyy-MM"
                includeDates={availableMonths} // Use the availableMonths array
              />
          </div>
        </div>
        <ShowChart data={data} attribute={"Suhu"} mode={"Avg"}/><br /><br />
        <ShowChart data={data} attribute={"RH"} mode={"Avg"}/><br /><br />
        <ShowChart data={KTData} attribute={"KT"} mode={"Avg"}/><br /><br />
        <ShowChart data={data} attribute={"Suhu"} mode={"Spot"}/><br /><br />
        <ShowChart data={data} attribute={"RH"} mode={"Spot"}/><br /><br />
        <ShowChart data={KTData} attribute={"KT"} mode={"Spot"}/><br /><br />
      </div>
    </div>
  );
}

export default MonthlyDataPage;
