import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import IconButton from '@mui/material/IconButton';
import Drawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import MenuIcon from '@mui/icons-material/Menu';
import DashboardIcon from '@mui/icons-material/Dashboard';
import EventIcon from '@mui/icons-material/Event';
import DownloadIcon from '@mui/icons-material/Download';
import Typography from '@mui/material/Typography';
import logoLeft from './images/logo-left.png';
import DataPage from './pages/DataPage'; // Import your data page component
import MonthlyDataPage from './pages/MonthlyDataPage'; // Import your monthly data page component
import DownloadPage from './pages/DownloadPage'; // Import your download page component
import Footer from './components/Footer';
import './css/App.css'; // Include your CSS for additional styling

function App() {
  const [drawerOpen, setDrawerOpen] = useState(false);

  const toggleDrawer = (open) => () => {
    setDrawerOpen(open);
  };

  const menuItems = [
    { icon: <DashboardIcon />, text: 'Dashboard', path: '/' },
    { icon: <EventIcon />, text: 'Monthly Data', path: '/monthly-data' },
    { icon: <DownloadIcon />, text: 'Download', path: '/download' },
    // Add more menu items here
  ];

  return (
    <Router basename={"/mon"}>
      <div className="App">
        <div className="app-header">
          <IconButton onClick={toggleDrawer(true)}>
            <MenuIcon />
          </IconButton>
          <img src={logoLeft} alt="Logo Left" className="logo-left" /> {/* Add logoLeft image */}
          <Typography variant="h6" className="app-title">
            Greenhouse Monitoring System (Kerja Sama YESSA - FTIP UNPAD)
          </Typography>
        </div>
        <Drawer anchor="left" open={drawerOpen} onClose={toggleDrawer(false)}>
          <div className="vertical-nav">
            <div className="logo-container">
              <img src={logoLeft} alt="Organization logo" className="logo" />
              <Typography variant="h6">
                Greenhouse Monitoring System
              </Typography>
            </div>
            <List>
              {menuItems.map((item, index) => (
                <ListItem button key={index} className="nav-item">
                  <ListItemIcon>{item.icon}</ListItemIcon>
                  <Link to={item.path} className="nav-link">
                    <ListItemText primary={item.text} className="nav-text" />
                  </Link>
                </ListItem>
              ))}
            </List>
          </div>
        </Drawer>
        <Routes>
          <Route path="/" element={<DataPage />} />
          <Route path="/monthly-data" element={<MonthlyDataPage />} />
          <Route path="/download" element={<DownloadPage />} />
          {/* Add more routes for other pages */}
        </Routes>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
