import { Line } from 'react-chartjs-2';
function ShowChart({ data, attribute, mode}) {
  // Initializing title and label text
  let titleText = '';
  let labelText = '';
  let axisTitle = '';
  let ticksText = '';

  // Based on the 'attribute' prop, setting label and title texts
  if (attribute === 'Suhu') {
    labelText = 'Temp'
    titleText = 'Average Temperature';
    axisTitle = 'Average Temperature';
    ticksText = '°C';    
  } else if (attribute === 'RH'){
    labelText = 'RH'
    titleText = 'Relative Humidity';
    axisTitle = 'Relative Humidity';
    ticksText = '%';
  } else if (attribute === 'KT'){
    labelText = 'SH'
    titleText = 'Soil Moisture Content';
    axisTitle = 'Soil Moisture Content';
    ticksText = '%';
  } 

  // Uncomment this part to use null-deleted data
  // this assume there's duplicate time
  const uniqueTime = data.filter((obj, index, self) => {
    const timestamp = obj.Waktu;
    return index === self.findIndex((o) => o.Waktu === timestamp);
  });

  // Initializing chartData object
  const chartData = {
    labels: uniqueTime.map((d) => d.Waktu), // labels: data.map (original), or replace data with uniqueTime (null-deleted)
    datasets: [],
  };
  
  // Uncomment this part to use interpolated data
  // const interpolateNullData = (data) => {
  //   const interpolatedData = [];
  
  //   for (let i = 0; i < data.length; i++) {
  //     if (data[i] === null) {
  //       let j = i + 1;
  //       while (j < data.length && data[j] === null) {
  //         j++;
  //       }
  //       const start = data[i - 1] || 0;
  //       const end = data[j] || 0;
  //       const step = (end - start) / (j - i + 1);
  
  //       for (let k = i; k < j; k++) {
  //         interpolatedData[k] = start + step * (k - i + 1);
  //       }
  
  //       i = j - 1;
  //     } else {
  //       interpolatedData[i] = data[i];
  //     }
  //   }
  
  //   return interpolatedData;
  // };

  // Uncomment this part to use null-deleted data
  // Filtering out null data
  const deleteNullData = (data) => {
    const filteredData = [];
  
    for (let i = 0; i < data.length; i++) {
      if (data[i] !== null) {
        filteredData.push(data[i]);
      }
    }
  
    return filteredData;
  };

  // Handling different chart modes
  if (mode === "Avg"){
    chartData.datasets.push({
      label: `Inside`,
      data: data.map((d) => d[`${attribute}_Mean`]),
    });
    chartData.datasets.push({
      label: `Outside`,
      data: data.map((d) => d[`${attribute}_Ref_Mean`]),
    });

    // Uncomment this part to use interpolated data
    // chartData.datasets[0].data = interpolateNullData(chartData.datasets[0].data);
    // chartData.datasets[1].data = interpolateNullData(chartData.datasets[1].data);

    // Uncomment this part to use null-deleted data
    chartData.datasets[0].data = deleteNullData(chartData.datasets[0].data);
    chartData.datasets[1].data = deleteNullData(chartData.datasets[1].data);
  
  } else if (mode === "Spot"){
    // Adding datasets for individual readings and references
    for (let i = 1; i <= 12; i++) {
      chartData.datasets.push({
        label: `In-${i}`,
        data: data.map((d) => d[`${attribute}${i}`]),
      });
    }
    
    for (let i = 1; i <= 4; i++) {
      chartData.datasets.push({
        label: `Out-${i}`,
        data: data.map((d) => d[`${attribute}_Ref${i}`]),
      });
    }
  }

  // Defining chartOptions for the Line chart
  const chartOptions = {
    scales: {
      x: {
        title: {
          display: true,
          text: 'Time (Hour:Minute:Second)',
        },
      },
      y: {
        beginAtZero: true,
        title: {
          display: true,
          text: axisTitle,
        },
        ticks: {
          callback: function(value) {
            return  value + ticksText;
          }
        }
      },
    },
    plugins: {
      title: {
        display: true,
        text: `${titleText}`,
        font: {
          size: 20,
        },
        padding: {
          bottom: 10,
        },
      },
      legend: {
        display: true,
        position: 'bottom',
      },
      tooltip: {
        mode: 'index',
        intersect: false,
        includeInvisible: true,
      },
    },

  };

  // Rendering the Line chart
  return <Line data={chartData} options={chartOptions} />;
}
export default ShowChart;